import { a as client } from "../../../../../chunks/connection.js";
import { V as ViewerSchema } from "../../../../../chunks/types.js";
import { r as redirect } from "../../../../../chunks/index.js";
const load = ({ locals }) => {
  if (locals.viewer) {
    throw redirect(302, "/respawn/streamersbattle/dashboard");
  }
};
const actions = {
  default: async ({ request }) => {
    const data = await request.formData();
    const name = data.get("name").trim();
    const email = data.get("email").trim();
    const phone = data.get("phone").trim();
    const password = data.get("password").trim();
    const parsed = ViewerSchema.safeParse({
      name,
      email,
      password,
      phone
    });
    if (parsed.success) {
      try {
        const existing = await client.viewers.findFirst({
          where: {
            email
          }
        });
        if (existing) {
          return {
            success: false,
            message: "User already exist"
          };
        }
        const created = await client.viewers.create({ data: {
          name,
          email,
          password,
          phone
        } });
        console.log(created);
      } catch (e) {
        console.log(e);
        return {
          success: false,
          error: "Failed to create viewer"
        };
      }
      throw redirect(302, "/respawn/streamersbattle/login");
    }
    return {
      success: false,
      error: parsed.error.format(),
      data: {
        name,
        email,
        password,
        phone
      }
    };
  }
};
export {
  actions,
  load
};
